
.visual-hidden{
  position: absolute;
  opacity: 0;
}

.section-right {
  position: relative;
  padding: 6rem;
  overflow: auto;

  .section-top {
    .title-h3 {
      position: relative;
      font-size: 2.6rem;
      line-height: 1;
      &::after {
        content: "";
        position: absolute;
        left: -1.5rem;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 1.4rem;
        height: 1.4rem;
        background-color: #3beab0;
        border-radius: 100%;
      }
    }
    .text {
      margin-top: 1.5rem;
      font-size: 1.8rem;
      line-height: 1;
    }
  }
}




@media screen and (min-width: 1200px) {
  .section-right {
    padding: 11.4rem 13.3rem 0;
  }
}